@import "./constants";

$width: $gridSize;
$height: $width * 1.4;

.Character {
  width: $width;
  height: $height;
  position: absolute;
  margin-top: $gridSize - $height;

  &.team_0 {
    background-image: url(images/characters/character_0.png);
  }

  &.team_1 {
    background-image: url(images/characters/character_1.png);
  }

  background-size: 400%;
  animation-name: walking-anim;
  animation-duration: .75s;
  animation-iteration-count: infinite;
  animation-timing-function: step-start;

  &:not(.moving) {
    animation: none;
  }

  &.moving {
    background-position-y: 0;
  }

  &.bottom {
    background-position-x: 0;
  }

  &.left {
    background-position-x: -$width;
  }

  &.top {
    background-position-x: -$width * 2;
  }

  &.right {
    background-position-x: -$width * 3;
  }
}

@keyframes walking-anim {
  0%, 100% {
    background-position-y: -$height;
  }

  25% {
    background-position-y: 0;
  }

  50% {
    background-position-y: $height;
  }

  75% {
    background-position-y: 0;
  }
}
